import img from "../../styles/img/ribbon.webp";
import imgText from "../../styles/img/logotext4.png";

import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";
import { useEffect } from "react";

export default function About() {
  const control = useAnimation();
  const control2 = useAnimation();
  const control3 = useAnimation();
  const control4 = useAnimation();
  const control5 = useAnimation();
  const control6 = useAnimation();

  const [ref, inView] = useInView();
  const [ref2, inView2] = useInView();
  const [ref3, inView3] = useInView();
  const [ref4, inView4] = useInView();
  const [ref5, inView5] = useInView();
  const [ref6, inView6] = useInView();

  const boxVariant = {
    visible: { y: 0, opacity: 1 },
    hidden: { y: 100, opacity: 0 },
  };

  useEffect(() => {
    if (inView) {
      control.start("visible");
    }
    if (inView2) {
      control2.start("visible");
    }
    if (inView3) {
      control3.start("visible");
    }
    if (inView4) {
      control4.start("visible");
    }
    if (inView5) {
      control5.start("visible");
    }
    if (inView6) {
      control6.start("visible");
    }
  }, [
    control,
    control2,
    control3,
    control4,
    control5,
    control6,
    inView,
    inView2,
    inView3,
    inView4,
    inView5,
    inView6,
  ]);

  return (
    <section id="about" className="about">
      <div className="about-title">
        <h2></h2>
      </div>
      <div className="about-box">
        <motion.div
          ref={ref}
          variants={boxVariant}
          animate={control}
          initial="hidden"
          transition={{ duration: 1 }}
          className="about-box-img"
        >
          <img src={img} alt="ribbon logo" />
          <img src={imgText} alt="text logo" />
        </motion.div>

        <div className="about-box-text">
          <motion.p
            ref={ref2}
            variants={boxVariant}
            animate={control2}
            initial="hidden"
            transition={{ duration: 1 }}
          >
            
            CHOCO
          </motion.p>
          <motion.p
            ref={ref3}
            variants={boxVariant}
            animate={control3}
            initial="hidden"
            transition={{ duration: 1 }}
          >
            CHOCO
            
          </motion.p>
          <motion.p
            ref={ref6}
            variants={boxVariant}
            animate={control6}
            initial="hidden"
            transition={{ duration: 1 }}
          >
            CHOCO
            
          </motion.p>
          <motion.p
            ref={ref4}
            variants={boxVariant}
            animate={control4}
            initial="hidden"
            transition={{ duration: 1 }}
          >
             JUDY CHOCO
             JUDY  CHOCO  CHOCO
             CHOCO
            
          </motion.p>
          <motion.p
            ref={ref5}
            variants={boxVariant}
            animate={control5}
            initial="hidden"
            transition={{ duration: 1 }}
          >
            JUDY  CHOCO  CHOCO
            CHOCO  JUDY
            
            CHOCO 
          </motion.p>
        </div>
      </div>
    </section>
  );
}
