import { uploadContactData } from "../http";
import { useMutation } from "@tanstack/react-query";

export default function Contact() {
  const { mutate } = useMutation({
    mutationFn: uploadContactData,
    onSuccess: () => window.confirm("!"),
  });

  function handleSubmit(e) {
    e.preventDefault();

    const fd = new FormData(e.target);

    const data = Object.fromEntries(fd.entries());

    console.log(data);

    mutate(data);

    e.target.reset();
  }
  return (
    <div className="contact">
      <h2>
        <ion-icon name="chatbubbles-outline"></ion-icon>
        <span></span>
      </h2>
      <span className="contact-subtitle"></span>
      <form onSubmit={handleSubmit} className="contact-form">
        <div className="contact-form-input">
          <label htmlFor="title">&#8727;</label>
          <input type="text" name="title" required />
        </div>
        <div className="contact-form-input">
          <label htmlFor="email"></label>
          <input type="email" name="email" />
        </div>
        <div className="contact-form-input">
          <label htmlFor="content">&#8727;</label>
          <textarea name="content" cols="30" rows="10" required></textarea>
        </div>
        <button className="btn"></button>
      </form>
    </div>
  );
}
